

.photos-list-enter {
    opacity: 0.01;
    will-change: opacity;
}

.photos-list-enter.photos-list-enter-active {
    opacity: 1;
    -webkit-transition: all 500ms ease-in;
    -o-transition: all 500ms ease-in;
    transition: all 500ms ease-in;
    will-change: opacity;
}

.photos-list-exit {
    opacity: 1;
    will-change: opacity;
}

.photos-list-exit.photos-list-exit-active {
    opacity: 0.01;
    -webkit-transition: all 500ms ease-in;
    -o-transition: all 500ms ease-in;
    transition: all 500ms ease-in;
    will-change: opacity;

}




