

.photo-preview-enter {
    opacity: 0.01;
    will-change: opacity;
}

.photo-preview-enter.photo-preview-enter-active {
    opacity: 1;
    -webkit-transition: all 500ms ease-in;
    -o-transition: all 500ms ease-in;
    transition: all 500ms ease-in;
    will-change: opacity;
}

.photo-preview-exit {
    opacity: 1;
    will-change: opacity;
}

.photo-preview-exit.photo-preview-exit-active {
    opacity: 0.01;
    -webkit-transition: all 500ms ease-in;
    -o-transition: all 500ms ease-in;
    transition: all 500ms ease-in;
    will-change: opacity;
}



